import React from 'react';
import get from 'lodash/get';
import { useFormik } from 'formik';
import Accordion from '@mui/material/Accordion';
import AccordionSummary from '@mui/material/AccordionSummary';
import AccordionDetails from '@mui/material/AccordionDetails';
import WarehouseIcon from '@mui/icons-material/Warehouse';
import ArrowDropDownIcon from '@mui/icons-material/ArrowDropDown';
import Box from '@mui/material/Box';
import Typography from '@mui/material/Typography';
import IconButton from '@mui/material/IconButton';
import ArrowUpwardIcon from '@mui/icons-material/ArrowUpward';
import ArrowDownwardIcon from '@mui/icons-material/ArrowDownward';
import AddIcon from '@mui/icons-material/Add';
import RemoveIcon from '@mui/icons-material/Remove';

import TimeFrameForm from './timeFrame';
import FreightListForm from './freightList';
import { SearchField, TextField, FileList } from '../components';

import { FacilitiesQuery } from '../api';
import { Facility, TimeFrameType } from '../utils/dataTypes';
import { stopPropagation } from '../utils/stopPropagation';

export default function StopPickUpForm({
  stopNumber,
  pickUpNumber,
  fieldName,
  onUp,
  onDown,
  onAdd,
  onRemove,
  upDisabled,
  downDisabled,
  removeDisabled,
  disabled,
  formik,
}: {
  readonly stopNumber: number;
  readonly pickUpNumber: number;
  readonly fieldName: string;
  readonly onUp: () => void;
  readonly onDown: () => void;
  readonly onAdd: () => void;
  readonly onRemove: () => void;
  readonly upDisabled?: boolean;
  readonly downDisabled?: boolean;
  readonly removeDisabled?: boolean;
  readonly disabled?: boolean;
  readonly formik: ReturnType<typeof useFormik<any>>;
}) {
  const id = get(formik.values, 'id');
  const stopId = get(formik.values, `${fieldName}.stopId`);

  const [expanded, setExpanded] = React.useState<boolean>(false);

  const handleExpandedChange = () => {
    setExpanded((prev) => !prev);
  };

  const handleUp = (e: React.SyntheticEvent) => {
    stopPropagation(e);
    onUp();
  };

  const handleDown = (e: React.SyntheticEvent) => {
    stopPropagation(e);
    onDown();
  };

  const handleAdd = (e: React.SyntheticEvent) => {
    stopPropagation(e);
    onAdd();
  };

  const handleRemove = (e: React.SyntheticEvent) => {
    stopPropagation(e);
    onRemove();
  };

  return (
    <Accordion
      expanded={expanded}
      onChange={handleExpandedChange}
      sx={{ backgroundColor: 'unset' }}
    >
      <AccordionSummary
        expandIcon={<ArrowDropDownIcon />}
        aria-controls="stop-pickup-content"
        id="stop-pickup-header"
      >
        <Box
          sx={{
            width: '100%',
            display: 'flex',
            justifyContent: 'space-between',
          }}
        >
          <Typography>{`Stop (${stopNumber}) PickUp #${pickUpNumber}`}</Typography>
          <Box>
            <IconButton
              component="label"
              aria-label="up"
              sx={{ padding: 0 }}
              onClickCapture={handleUp}
              disabled={disabled || upDisabled}
            >
              <ArrowUpwardIcon />
            </IconButton>
            <IconButton
              component="label"
              aria-label="down"
              sx={{ padding: 0 }}
              onClickCapture={handleDown}
              disabled={disabled || downDisabled}
            >
              <ArrowDownwardIcon />
            </IconButton>
            <IconButton
              component="label"
              aria-label="add"
              sx={{ padding: 0 }}
              onClickCapture={handleAdd}
              disabled={disabled}
            >
              <AddIcon />
            </IconButton>
            <IconButton
              component="label"
              aria-label="remove"
              sx={{ padding: 0 }}
              onClickCapture={handleRemove}
              disabled={disabled || removeDisabled}
            >
              <RemoveIcon />
            </IconButton>
          </Box>
        </Box>
      </AccordionSummary>
      <AccordionDetails>
        <SearchField<Facility>
          fieldName={`${fieldName}.facility`}
          label="Facility"
          getSelectedLabel={(selectedItem) => `${selectedItem.name}`}
          getItemLabel={(item) => `${item.name}`}
          getItemSecondLabel={(item) => `${item.address}`}
          ListIcon={WarehouseIcon}
          entityType="Facility"
          queryFn={FacilitiesQuery}
          startEmpty={false}
          orderby="name"
          order="asc"
          disabled={disabled}
          formik={formik}
        />
        <TimeFrameForm
          fieldName={`${fieldName}.timeFrame`}
          values={[TimeFrameType.FCFS, TimeFrameType.APPT, TimeFrameType.ASAP]}
          disabled={disabled}
          formik={formik}
        />
        <FreightListForm
          fieldName={`${fieldName}.freightList`}
          disabled={disabled}
          formik={formik}
        />
        <TextField
          fieldName={`${fieldName}.addInfo`}
          label="Additional stop info"
          disabled={disabled}
          formik={formik}
        />
        <FileList
          objectId={id}
          objectType="Load"
          label="Stop Files"
          tags={{ [`${stopId}`]: 'StopPickUp' }}
          disabled={disabled}
        />
      </AccordionDetails>
    </Accordion>
  );
}
