import React from 'react';
import get from 'lodash/get';
import { useFormik } from 'formik';
import Box from '@mui/material/Box';

import { Freight } from '../utils/dataTypes';
import FreightForm from './freight';
import { initialFreightValues } from './createLoad';

export default function FreightListForm({
  fieldName,
  disabled,
  formik,
}: {
  readonly fieldName: string;
  readonly disabled?: boolean;
  readonly formik: ReturnType<typeof useFormik<any>>;
}) {
  const formikValue = get(formik.values, fieldName);

  const onAdd = (index: number): void => {
    const newValue = formikValue.toSpliced(index + 1, 0, initialFreightValues);
    formik.setFieldValue(fieldName, newValue);
  };
  const onRemove = (index: number): void => {
    const newValue = formikValue.toSpliced(index, 1);
    formik.setFieldValue(fieldName, newValue);
  };

  return (
    <Box key="freightList" sx={{ paddingBottom: 3, width: '100%' }}>
      {formikValue.map((freight: Freight, index: number) => (
        <FreightForm
          key={index}
          number={index + 1}
          fieldName={`${fieldName}[${index}]`}
          onAdd={() => onAdd(index)}
          onRemove={() => onRemove(index)}
          removeDisabled={formikValue.length === 1}
          disabled={disabled}
          formik={formik}
        />
      ))}
    </Box>
  );
}
