import React from 'react';
import { useFormik } from 'formik';
import Accordion from '@mui/material/Accordion';
import AccordionSummary from '@mui/material/AccordionSummary';
import AccordionDetails from '@mui/material/AccordionDetails';
import ArrowDropDownIcon from '@mui/icons-material/ArrowDropDown';
import Box from '@mui/material/Box';
import Typography from '@mui/material/Typography';
import IconButton from '@mui/material/IconButton';
import AddIcon from '@mui/icons-material/Add';
import RemoveIcon from '@mui/icons-material/Remove';

import { SelectField, TextField } from '../components';

import { UnitsOfLength, UnitsOfWeight } from '../utils/dataTypes';
import { stopPropagation } from '../utils/stopPropagation';

export default function FreightForm({
  number,
  fieldName,
  onAdd,
  onRemove,
  removeDisabled,
  disabled,
  formik,
}: {
  readonly number: number;
  readonly fieldName: string;
  readonly onAdd: () => void;
  readonly onRemove: () => void;
  readonly removeDisabled?: boolean;
  readonly disabled?: boolean;
  readonly formik: ReturnType<typeof useFormik<any>>;
}) {
  const [expanded, setExpanded] = React.useState<boolean>(false);

  const handleExpandedChange = () => {
    setExpanded((prev) => !prev);
  };

  const handleAdd = (e: React.SyntheticEvent) => {
    stopPropagation(e);
    onAdd();
  };

  const handleRemove = (e: React.SyntheticEvent) => {
    stopPropagation(e);
    onRemove();
  };

  return (
    <Accordion
      expanded={expanded}
      onChange={handleExpandedChange}
      sx={{ backgroundColor: 'unset' }}
    >
      <AccordionSummary
        expandIcon={<ArrowDropDownIcon />}
        aria-controls="freight-content"
        id="freight-header"
      >
        <Box
          sx={{
            width: '100%',
            display: 'flex',
            justifyContent: 'space-between',
          }}
        >
          <Typography>{`Freight #${number}`}</Typography>
          <Box>
            <IconButton
              component="label"
              aria-label="add"
              sx={{ padding: 0 }}
              onClickCapture={handleAdd}
              disabled={disabled}
            >
              <AddIcon />
            </IconButton>
            <IconButton
              component="label"
              aria-label="remove"
              sx={{ padding: 0 }}
              onClickCapture={handleRemove}
              disabled={disabled || removeDisabled}
            >
              <RemoveIcon />
            </IconButton>
          </Box>
        </Box>
      </AccordionSummary>
      <AccordionDetails>
        <TextField
          fieldName={`${fieldName}.pieces`}
          label="Pieces"
          number
          disabled={disabled}
          formik={formik}
        />
        <Box key="inlineControls" sx={{ display: 'flex', width: '100%' }}>
          <Box key="left" sx={{ paddingRight: 1, flex: 1 }}>
            <SelectField
              fieldName={`${fieldName}.unitOfWeight`}
              label="Unit Of Weight"
              items={UnitsOfWeight}
              disabled={disabled}
              formik={formik}
            />
            <TextField
              fieldName={`${fieldName}.weight`}
              label="Weight"
              number
              disabled={disabled}
              formik={formik}
            />
          </Box>
          <Box key="right" sx={{ paddingLeft: 1, flex: 1 }}>
            <SelectField
              fieldName={`${fieldName}.unitOfLength`}
              label="Unit Of Length"
              items={UnitsOfLength}
              disabled={disabled}
              formik={formik}
            />
            <TextField
              fieldName={`${fieldName}.length`}
              label="Length"
              number
              disabled={disabled}
              formik={formik}
            />
          </Box>
        </Box>
      </AccordionDetails>
    </Accordion>
  );
}
