import React from 'react';
import get from 'lodash/get';
import { useFormik } from 'formik/dist/Formik';
import Box from '@mui/material/Box';

import StopPickUpForm from './stopPickUp';
import StopDeliveryForm from './stopDelivery';
import {
  initialStopDeliveryValues,
  initialStopPickUpValues,
} from './createLoad';
import { StopDelivery, StopPickUp, StopType } from '../utils/dataTypes';

export default function StopsForm({
  fieldName,
  disabled,
  formik,
}: {
  readonly fieldName: string;
  // stops: (StopPickUp | StopDelivery)[];
  readonly disabled?: boolean;
  readonly formik: ReturnType<typeof useFormik<any>>;
}) {
  const stops = get(formik.values, fieldName) as (StopPickUp | StopDelivery)[];

  const onUp = (index: number): void => {
    const upElement = stops[index - 1];
    const thisElement = stops[index];
    const newValue = stops.toSpliced(index - 1, 2, thisElement, upElement);
    formik.setFieldValue(fieldName, newValue);
  };
  const onDown = (index: number): void => {
    const thisElement = stops[index];
    const downElement = stops[index + 1];
    const newValue = stops.toSpliced(index, 2, downElement, thisElement);
    formik.setFieldValue(fieldName, newValue);
  };
  const onAddPickUp = (index: number): void => {
    const newValue = stops.toSpliced(index + 1, 0, initialStopPickUpValues);
    formik.setFieldValue(fieldName, newValue);
  };
  const onAddDelivery = (index: number): void => {
    const newValue = stops.toSpliced(index + 1, 0, initialStopDeliveryValues);
    formik.setFieldValue(fieldName, newValue);
  };
  const onRemove = (index: number): void => {
    const newValue = stops.toSpliced(index, 1);
    formik.setFieldValue(fieldName, newValue);
  };

  return (
    <Box key="stops" sx={{ paddingBottom: 3, width: '100%' }}>
      {!stops
        ? null
        : stops.map((stop, index) => {
            switch (stop.type) {
              case StopType.PickUp:
                return (
                  <StopPickUpForm
                    key={index}
                    stopNumber={index + 1}
                    pickUpNumber={
                      stops
                        .slice(0, index + 1)
                        .filter((stop) => stop.type === StopType.PickUp).length
                    }
                    fieldName={`${fieldName}[${index}]`}
                    onUp={() => onUp(index)}
                    onDown={() => onDown(index)}
                    onAdd={() => onAddPickUp(index)}
                    onRemove={() => onRemove(index)}
                    upDisabled={index === 0}
                    downDisabled={
                      index === stops.length - 2 ||
                      (index === 0 && stops[1].type === StopType.Delivery)
                    }
                    removeDisabled={index === 0}
                    disabled={disabled}
                    formik={formik}
                  />
                );
              case StopType.Delivery:
                return (
                  <StopDeliveryForm
                    key={index}
                    stopNumber={index + 1}
                    deliveryNumber={
                      stops
                        .slice(0, index + 1)
                        .filter((stop) => stop.type === StopType.Delivery)
                        .length
                    }
                    fieldName={`${fieldName}[${index}]`}
                    onUp={() => onUp(index)}
                    onDown={() => onDown(index)}
                    onAdd={() => onAddDelivery(index)}
                    onRemove={() => onRemove(index)}
                    upDisabled={
                      (index === stops.length - 1 &&
                        stops[stops.length - 2].type === StopType.PickUp) ||
                      index === 1
                    }
                    downDisabled={index === stops.length - 1}
                    removeDisabled={index === stops.length - 1}
                    disabled={disabled}
                    formik={formik}
                  />
                );
            }
          })}
    </Box>
  );
}
