import React from 'react';
import get from 'lodash/get';
import { useFormik } from 'formik';
import Box from '@mui/material/Box';

import { DateTimeField, SelectField } from '../components';

import { TimeFrameType } from '../utils/dataTypes';

export default function TimeFrameForm({
  fieldName,
  values,
  disabled,
  formik,
}: {
  readonly fieldName: string;
  readonly values: TimeFrameType[];
  readonly disabled?: boolean;
  readonly formik: ReturnType<typeof useFormik<any>>;
}) {
  const formikValue = get(formik.values, fieldName);

  React.useEffect(() => {
    if (formikValue.type === TimeFrameType.FCFS) {
      (formikValue.at || formikValue.at === null) &&
        formik.setFieldValue(`${fieldName}.at`, undefined);
      !formikValue.from && formik.setFieldValue(`${fieldName}.from`, null);
      !formikValue.to && formik.setFieldValue(`${fieldName}.to`, null);
    } else if (formikValue.type) {
      !formikValue.at && formik.setFieldValue(`${fieldName}.at`, null);
      (formikValue.from || formikValue.from === null) &&
        formik.setFieldValue(`${fieldName}.from`, undefined);
      (formikValue.to || formikValue.to === null) &&
        formik.setFieldValue(`${fieldName}.to`, undefined);
    }
  }, [formikValue.type]);

  return (
    <Box key="inlineControls" sx={{ display: 'flex', width: '100%' }}>
      <Box key="typeContainer" sx={{ paddingRight: 1, flex: 1 }}>
        <SelectField
          fieldName={`${fieldName}.type`}
          label="Time frame type"
          items={values}
          disabled={disabled}
          formik={formik}
        />
      </Box>
      {formikValue.type === TimeFrameType.FCFS ? (
        <>
          <Box
            key="fromContainer"
            sx={{ paddingRight: 1, paddingLeft: 1, flex: 1 }}
          >
            <DateTimeField
              fieldName={`${fieldName}.from`}
              label="From"
              minDateTime={new Date(Date.now() - 24 * 60 * 60 * 1000)}
              disabled={disabled}
              formik={formik}
            />
          </Box>
          <Box key="toContainer" sx={{ paddingLeft: 1, flex: 1 }}>
            <DateTimeField
              fieldName={`${fieldName}.to`}
              label="To"
              minDateTime={new Date(Date.now() - 24 * 60 * 60 * 1000)}
              disabled={disabled}
              formik={formik}
            />
          </Box>
        </>
      ) : (
        <Box key="atContainer" sx={{ paddingLeft: 1, flex: 1 }}>
          <DateTimeField
            fieldName={`${fieldName}.at`}
            label="At"
            minDateTime={new Date(Date.now() - 24 * 60 * 60 * 1000)}
            disabled={disabled}
            formik={formik}
          />
        </Box>
      )}
    </Box>
  );
}
